.header {
    display: flex;
    width: 880px;
    min-height: 56px;
    margin: 0 auto;
    justify-content: space-between;
    align-items: baseline;
    padding-top: 45px;
    padding-bottom: 41px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 910px) {
    .header {
        max-width: 568px;
      }
}

@media screen and (max-width: 586px) {
    .header {
        max-width: 320px;
        padding-top: 28px;
        padding-bottom: 32px;
        flex-direction: column;
    }
}
  

